.coaches {
    position: relative;
    overflow: hidden;
    padding: 54px 0;
    padding-bottom: 140px;
    background-color: $color-default-white;

    @include vp-1199 {
        padding: 50px 56px;
        padding-bottom: 100px;
    }

    @include vp-767 {
        padding: 30px 32px;
        padding-bottom: 70px;
    }
}

.coaches h4 {
    margin: 0;
    margin-bottom: 50px;
    font-family: $ff-alumni-sans;
    font-weight: 600;
    font-size: 54px;
    line-height: 65px;
    letter-spacing: 0.03em;
    text-align: left;

    @include vp-1199 {
        font-size: 42px;
        line-height: 50px;
        margin-bottom: 32px;
        text-align: center;
    }

    @include vp-767 {
        font-size: 32px;
        line-height: 38px;
        margin-bottom: 42px;
    }
}