.slider {
    position: relative;
}

.slider--reviews {
    width: 60%;
    margin: auto;

    @include vp-1199 {
        width: 84%;
     }

     @include vp-767 {
        width: 78%;
     }
}

.slider__list {
    position: relative;
    display: flex;
}

.slider-arrows {
    position: absolute;
    content: "";
    height: fit-content;
    top: 52%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;

    @include vp-1199 {
        top: 50%;
      }
}

.slider-arrows--reviews {
    top: 50%;
}

.slider__arrow {
    position: absolute;
    content: "";
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    width: 40px;
    height: 40px;
    outline: none;
    background-color: transparent;
    cursor: pointer;

    @include vp-767 {
        width: 35px;
        height: 35px;
       }
}

.slider__arrow--left {
    left: -70px;
    right: auto;

    @include vp-767 {
        left: -45px;
       }
}

.slider__arrow--left-review {
    left: 0;

    @include vp-1199 {
        left: -70px;
    }

    @include vp-767 {
       left: -45px;
    }
}

.slider__arrow--right {
    right: -70px;
    left: auto;
    
    @include vp-767 {
        right: -45px;
       }
}

.slider__arrow--right-review {
    right: 0;

    @include vp-1199 {
        right: -70px;
    }

    @include vp-767 {
        right: -45px;
    }
}

.swiper-button-disabled {
    opacity: 0.4;
    pointer-events: none;
  }

.slider__icon {
    @include vp-767 {
       width: 10px;
       height: 19px;
      }
}

.slider__icon--next {
    transform: rotate(180deg);
}