.competitions {
    padding-top: 120px;
    padding-bottom: 52px;
    background-color: $color-default-white;

    @include vp-1199 {
        padding-top: 100px;
        padding-bottom: 50px;
    }

    @include vp-767 {
        padding-top: 72px;
        padding-bottom: 35px;
    }
}

.competitions__wrapper {
    display: flex;

    @include vp-767 {
        flex-direction: column;
    }
}

.competitions__content {
    width: 43%;
    padding: 80px 78px;
    padding-bottom: 85px;
    background-color: $color-red;
    color: $color-text-white;

    @include vp-1199 {
        padding: 45px 45px;
    }

    @include vp-767 {
        width: 100%;
        padding-bottom: 49px;
        text-align: center;
    }
}

.competitions__content p {
    margin: 0;
    margin-bottom: 47px;
    font-size: 18px;
    line-height: 150%;

    @include vp-1199 {
        font-size: 12px;
        margin-bottom: 29px;
    }

    @include vp-767 {
        text-align: left;
    }
}

.competitions__info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 25px;
    margin-bottom: 8px;

    @include vp-1199 {
        margin-bottom: 1px;
        gap: 15px;
    }

    @include vp-767 {
        margin-bottom: 0;
    }
}

.competitions__info h4 {
    margin: 0;
    font-family: $ff-alumni-sans;
    font-weight: 600;
    font-size: 82px;
    line-height: 96%;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    transform: translate(13px, -5px);

    @include vp-1199 {
        font-size: 48px;
        transform: translate(7px, -2px);
    }
}

.competitions__list {
    margin: 0;
    padding: 0;
}

.competitions__termin {
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;

    @include vp-1199 {
        font-size: 12px;
    }
}

.competitions__description {
    position: relative;
    display: flex;
    align-items: center;
    gap: 5px;
    width: fit-content;
    margin: 0;
    font-weight: 700;
    font-size: 40px;
    line-height: 150%;

    @include vp-1199 {
        font-size: 24px;
    }
}

.competitions__description:first-of-type {
    margin-bottom: 10px;

    @include vp-1199 {
        margin-bottom: 0;
    }
}

.competitions__description::before {
    position: absolute;
    content: "";
    top: 6px;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $color-default-white;

    @include vp-1199 {
        top: 1px;
    }
}

.competitions__description small {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;

    @include vp-1199 {
        font-size: 10px;
    }
}

.competitions__link {
    width: 100%;
    padding: 25px;

    @include vp-1199 {
        line-height: 10px;
        font-size: 12px;
        padding: 13px;
        text-transform: lowercase;
    }

    @include vp-767 {
        max-width: 300px;
    }
}

.competitions__image-wrapper {
    position: relative;
    z-index: 2;
    width: 57%;

    @include vp-767 {
       width: 100%;
    }
}

.competitions__image-wrapper::before {
    position: absolute;
    z-index: -1;
    content: "";
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: $color-text-blue;
}

.competitions__image-wrapper img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}