.contacts {
    position: relative;
    display: flex;
    justify-content: center;
    padding-top: 56px;
    padding-left: 120px;
    padding-bottom: 63px;
    background-color: $color-default-white;

    @include vp-1199 {
        padding-top: 61px;
        padding-left: 132px;
        padding-right: 30px;
        padding-bottom: 62px;
    }

    @include vp-767 {
        padding-top: 64px;
        padding-bottom: 64px;
        padding-left: 0;
    }
}

.contacts::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #74819c;

    @include vp-1199 {
        display: none;
    }
}

.contacts__wrapper {
    width: 100%;
    max-width: 800px;

    @include vp-1199 { 
        max-width: 600px;
      }

      @include vp-767 { 
        padding: 0 15px;
      }
}

.contacts h6 {
    margin: 0;
    margin-bottom: 37px;
    font-family: $ff-alumni-sans;
    font-weight: 600;
    font-size: 54px;
    line-height: 65px;
    letter-spacing: 0.03em;

    @include vp-1199 {
        margin-bottom: 33px;
        font-size: 42px;
        line-height: 50px;
     }

     @include vp-767 {
        margin-bottom: 35px;
        font-size: 32px;
        line-height: 38px;
     }
}

.contacts__list {
    padding-left: 5px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20px;
    row-gap: 17px;

    @include vp-1199 {
        padding-left: 0;
        row-gap: 20px;
    }

    @include vp-767 {
        grid-template-columns: 1fr;
    }
}

.contacts__list h7 {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;

    @include vp-1199 {
        font-size: 20px;
     }
}

.contacts__list p {
    margin: 0;
    margin-top: 5px;
    font-size: 14px;
    line-height: 21px;

    @include vp-1199 {
        font-size: 18px;
        line-height: 27px;
     }
}

.contacts__phone {
    @include vp-767 {
        grid-row: 3/4;
     }
}