.advantages {
    padding-top: 97px;
    padding-bottom: 103px;
    background-color: $color-light-grey;

    @include vp-1199 {
        padding-top: 91px;
        padding-bottom: 48px;
    }

    @include vp-767 {
        padding-top: 63px;
        padding-bottom: 50px;
    }
}
.advantages__container {
    @include vp-767 {
        padding: 0;
    }
}

.advantages h5 {
    margin: 0;
    margin-bottom: 13px;
    font-family: $ff-alumni-sans;
    font-style: normal;
    font-weight: 600;
    font-size: 54px;
    line-height: 65px;
    letter-spacing: 0.03em;
    text-align: center;

    @include vp-1199 {
        margin-bottom: 12px;
        font-size: 42px;
        line-height: 50px;
        transform: translateX(-10px);
    }

    @include vp-767 {
        font-size: 32px;
        line-height: 38px;
        margin-bottom: 32px;
    }
}

