.advantages__list {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 86px;
    column-gap: 283px;
    padding-left: 80px;

    @include vp-1199 {
        padding-left: 0;
        column-gap: 32px;
        row-gap: 6px;
        grid-template-rows: repeat(3, auto);
    }

    @include vp-767 {
        // grid-template-columns: 1fr;
        // grid-template-rows: auto;
        // row-gap: 0
        display: flex;
        flex-direction: column;
        row-gap: 0;
        align-items: center;
    }
}

.advantages__item {
    min-height: 150px;
    display: flex;
    gap: 30px;
    padding-top: 30px;
    padding-bottom: 30px;

    @include vp-767 {
        flex-direction: column;
        align-items: center;
        padding: 15px;
        padding-left: 10px;
        padding-right: 10px;
        justify-self: center;
        width: 77%;
        gap:0;
    }
}

// .advantages__item:nth-of-type(2n+1) {

// }

.advantages__item-inner {
    display: flex;
    flex-direction: column;
    flex-shrink: 0;

    @include vp-767 {
        align-items: center;
        margin-bottom: 5px;
    }
}

.advantages__item-inner span {
    font-weight: 700;
    font-size: 71px;
    line-height: 95%;
    background: linear-gradient(142.51deg, #D10A42 0%, #0041CA 87.33%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;

    @include vp-1199 {
        font-size: 58px;
    }

    @include vp-767 {
        line-height: 88%;
    }

}

.advantages__item-inner small {
    font-weight: 300;
    font-size: 16px;
    line-height: 140%;
    color: #2e35ac;
}

.advantages__item-content {
    padding-top: 3px;
    padding-right: 40px;

    @include vp-1199 {
        padding-right: 8px;
    }

    @include vp-767 {
        text-align: center;
        padding-top: 0;
        padding-right: 0;
    }
}

.advantages__item-content h5 {
    font-family: "RobotoCondensed";
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 22px;
    line-height: 150%;
    text-align: left;
    letter-spacing: 0;
    transform: none;

    @include vp-1199 {
        font-size: 20px;
        margin-bottom: 0;
    }

    @include vp-767 {
       text-align: center;
    }
}

.advantages__item-content p {
    margin: 0;
    font-weight: 300;
    font-size: 16px;
    line-height: 140%;
}

.advantages__item--square {
    
    @include vp-767 {
        order: 1;
        margin-bottom: 9px;
     }
}

.advantages__item--square span {
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.advantages__item--parking {
    @include vp-767 {
        order: 2;
        margin-bottom: 30px;
     }
}

.advantages__item--comfort {
    @include vp-767 {
        order: 4;
        margin-bottom: 8px;
     }
}

.advantages__item--experience {
    @include vp-767 {
        order: 5;
        margin-bottom: 9px;
        padding-right: 0;
        padding-left: 0;
     }
}

.advantages__item:last-of-type {
    @include vp-767 {
        margin-bottom: 0;
    }
}

.advantages__item--center {
    position: absolute;
    content: "";
    top: 52%;
    left: 50%;
    transform: translate(-50%, -52%);
    flex-direction: column;
    display: flex;
    flex-direction: column;
    max-width: 240px;
    min-height: auto;
    height: 80%;
    padding-right: 15px;
    padding-left: 15px;
    padding-top: 53px;
    padding-bottom: 15px;
    overflow-y: auto;
    text-align: center;
    background-color: $color-blue;
    color: $color-default-white;

    @include vp-1199 {
        display: block;
        position: static;
        justify-self: center;
        transform: none;
        max-width: 65%;
        min-height: 239px;
        height: auto;
        grid-row-start: 2;
        grid-row-end: 3;
        grid-column-start: 1;
        grid-column-end: 3;
        padding-top: 22px;
        padding-bottom: 44px;
    }

    @include vp-767 {
        // grid-row-start: 3;
        // grid-row-end: 4;
        // grid-column-start: 1;
        // grid-column-end: 2;
        max-width: 100%;
        min-height: 239px;
        padding-right: 0;
        padding-left: 0;
        padding-top: 37px;
        width: 100%;
        order:3;
        margin-bottom: 30px;
    }
}
.advantages__item--center .advantages__item-content {
    padding: 0;

    @include vp-1199 {
        padding-right: 100px;
        padding-left: 100px;
    }

    @include vp-767 {
        padding-left: 55px;
        padding-right: 55px;
    }
}

.advantages__item--center span {
    background: unset;
    background-clip: unset;
    -webkit-background-clip: unset;
    -webkit-text-fill-color: unset;
    color: $color-text-white;
    font-weight: 700;
    font-size: 74px;
    line-height: 106%;
    color: $color-text-white;

    @include vp-767 {
        line-height: 94%;
    }
}

.advantages__item--center small {
    font-weight: 700;
    font-size: 26px;
    line-height: 150%;
    color: $color-text-white;

    @include vp-1199 {
        font-size: 22px;
        margin-bottom: 15px;
    }

    @include vp-767 {
       margin-bottom: 4px;
    }
}

.advantages__item--center p {
    color: $color-text-white;
}








