.reviews {
    padding-top: 66px;
    padding-bottom: 121px;
    background-color: $color-default-white;

    @include vp-1199 {
        padding-top: 41px;
        padding-bottom: 101px;
     }

     @include vp-767 {
        padding-top: 30px;
        padding-bottom: 72px;
     }
}

.reviews__wrapper {
    width: 80%;
    margin: auto;

    @include vp-1199 {
        width: 100%;
     }
}

.reviews h5 {
    margin: 0;
    margin-bottom: 37px;
    transform: translateX(4px);
    font-family: $ff-alumni-sans;
    font-weight: 600;
    font-size: 54px;
    line-height: 65px;
    letter-spacing: 0.03em;
    text-align: center;
    
    @include vp-1199 {
        margin-bottom: 39px;
        font-size: 42px;
        line-height: 50px;
        transform: none;
     }

     @include vp-767 {
        font-size: 32px;
        line-height: 38px;
     }
}

.reviews__item {
    max-width: 100%;
    display: flex;
    gap: 27px;
    padding: 62px;
    padding-bottom: 49px;
    padding-right: 58px;
    background-color: $color-text-blue;
    color: $color-default-white;

    @include vp-1199 {
       padding-left: 69px;
     }

     @include vp-767 {
      flex-direction: column;
      align-items: center;
      gap: 23px;
      padding-top: 40px;
      padding-bottom: 25px;
      padding-right: 25px;
      padding-left: 22px;
    }
}

.reviews__image-header {
    flex-shrink: 0;

    @include vp-767 {
        align-self: flex-start;
        padding-left: 15px;
        display: flex;
        align-items: center;
        gap: 20px;
      }
}

.reviews__image-wrapper {

    @include vp-767 {
        width: 68px;
        height: 68px;
      }
}

.reviews__image-wrapper img {
    object-fit: contain;

    @include vp-767 {
        width: 68px;
        height: 68px;
    }
}

.reviews__content {
    transform: translateY(-7px);
}

.reviews__content h5 {
    @include vp-767 {
        display: none;
    }
}


.reviews__content h5 {
    margin-bottom: 11px;
    font-family: $ff-roboto;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: $color-text-white;
    text-align: left;
    letter-spacing: normal;

    @include vp-767 {
        display: none;
     }
}

.reviews__item p {
    margin: 0;
    font-size: 16px;
    line-height: 140%;
    color: $color-text-white;

    
    @include vp-767 {
        text-align: left;
     }

     @media (max-width: 500px) {
        text-align: center;
     }
}

.reviews__image-header h5 {
    display: none;

    @include vp-767 {
        display: block;
        margin: 0;
        font-family: $ff-roboto;
        font-weight: 700;
        font-size: 20px;
        line-height: 130%;
        color: $color-text-white;
        text-align: left;
        letter-spacing: normal;
    }
}