.sign-up-form {
  @include vp-767 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 400px;
    flex-shrink: 0;
    width: 100%;
  }
}

.sign-up-form form {
    display: flex;
    flex-direction: column;

    @include vp-1199 {
      align-items: flex-start;
    }

    @include vp-767 {
      flex-shrink: 0;
       width: 100%;

    }
  }
  
  
  .sign-up-form label {
    position: relative;
    cursor: pointer;

    @include vp-767 {
      width: 100%;
      max-width: 400px;
    }
  }

  .sign-up-form__input-wrapper label::after {
    position: absolute;
    content: "";
    bottom: 2px;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $color-text-white;
  }
  
  
  .sign-up-form input:hover:not(:focus-visible) {
    opacity: 0.6;
  }
  
  .sign-up-form input:focus-visible {
    box-shadow: 0 2px 4px #e2eaf7;
  }
  
  .sign-up-form input:active {
    opacity: 1;
  }
  
  .sign-up-form__input-wrapper {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 30px;
    width: 100%;
    margin-bottom: 26px;
  
    @include vp-1199 {
      margin-bottom: 25px;
      gap: 62px;
    }

    @include vp-767 {
      flex-direction: column;
      align-items: center;
      gap: 23px;
      margin-bottom: 35px;
    }
  }
  
  .sign-up-form__input-wrapper input {
    min-width: 190px;
    box-sizing: border-box;
    padding: 10px 15px;
    padding-left: 0;
    color: $color-text-white;
    background-color: transparent;
    border: none;
    outline: none;
    caret-color: $color-text-white;
  
    @include vp-1199 {
        min-width: 206px;
        max-width: 48%;
    }

    @include vp-767 {
     min-width: unset;
     max-width: unset;
     width: 100%;
  }
  }

  .sign-up-form input::placeholder {
    font-size: 14px;
    line-height: 16px;
    color: $color-text-white
  }
  
  
  .sign-up-form__button {
    width: 100%;
    max-width: 410px;
    padding: 15px;
    font-size: 14px;
    line-height: 16px;

    
    @include vp-1199 {
        max-width: 472px;
    }

    @include vp-767 {
      max-width: 400px;
  }

    @include hover {
      opacity: 0.8;
    }
  
    @include focus {
      border: none;
      box-shadow: 0 2px 4px #e2eaf7;
    }
  }
  
  .sign-up-form label {
    position: relative;
  
    &.is-invalid {
      input,
      textarea {
        border: none;
        outline: 2px solid #ff1553;
  
        &:focus,
        &:hover {
          border: none;
          outline: 2px solid rgba(#ff1553, 0.5);
        }
      }
    }
  
    &.is-valid {
      input {
        border: none;
        outline: 2px solid #22b259;
  
        &:focus,
        &:hover {
          border: none;
          outline: 2px solid rgba(#22b259, 0.5);
        }
      }
    }
  
    .input-message {
      position: absolute;
      left: 0;
      bottom: -22px;
  
      &.is-invalid {
        opacity: 1;
        font-size: 14px;
        color: #ff0000;
      }
  
      &.is-valid {
        color: #15ab07;
      }
    }
  }